
/* Font Types */

.poppins {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 500;
	font-size: 1.2rem;
	line-height: 2rem;
	text-transform: capitalize;
	color: var(--color-heading);
  }

  .poppins-w400 {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 400;
	font-size: 0.9rem;
	line-height: 1.4rem;
	text-transform: capitalize;
	color: var(--color-muted-test);
  }
  
  .poppins-heading {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 700;
	font-size: 1.7rem;
	line-height: 54px;
	text-transform: capitalize;
	color: var(--color-heading);
	
  }
  
  .poppins-sub-heading {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 700;
	font-size: 1.2rem;
	line-height: 36px;
	text-transform: capitalize;
	color: var(--color-heading);
  }
  
  .poppins-sub-heading-1rem {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 600;
	font-size: 1rem;
	line-height: 1rem;
	text-transform: capitalize;
	color: var(--color-heading);
  }
  
  .inter-heading {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 700;
	font-size: 1.4rem;
	line-height: 28px;
	text-transform: capitalize;
  }
  
  .inter-regular {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 400;
	font-size: 0.9rem;
	line-height: 28px;
	text-transform: capitalize;
  }
  
  
  .Montserrat {
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 500;
	font-size: 1.4rem;
	line-height: 24px;
	text-transform: capitalize;
  }


